import { action } from "../action/action";

var initialState = {
 data:{
    
 }
};

function Alldata(state = initialState, action) {
  switch (action.type) {
    
    case 'adddatachief':{

      console.log('data at reducre',action.data);

      return {
        ...state,
        data:action.data
      }}
      case 'adddataofficeboy':{

        console.log('data at reducre',action.data);
  
        return {
          ...state,
          data:action.data
        }
      }

    
    default:
      return state;
  }
}

export default Alldata;
