
import React, { Component } from 'react'
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Button from '@material-ui/core/Button';
import {connect} from 'react-redux';
import {adddatachief} from "../../redux/action/action"
import Home from "../mainbody/home"
import { stat } from 'fs';




 class Chief extends Component {
  constructor(props){
    super(props);
    this.state={
      
      title:"job for chief engineer",
      firstname:"",
      lastname:"",
      phonenumber:"",
      email:"",
      address1:"",
      address2:"",
      city:"",
      state:"",
      zip:"",
      country:"",
      checked:"",
    }
     
  }

  
  submitbtn=()=>{
    if(this.state.firstname==""){alert("PLEASE WRITE YOUR FIRSTNAME")}
    if(this.state.lastname==""){alert("PLEASE WRITE YOUR LASTNAME")}
    if(this.state.phonenumber==""){alert("PLEASE WRITE YOUR PHONE NUMBER")}
    if(this.state.email=="" ){alert("PLEASE WRITE YOUR E MAIL")}
    if(this.state.address1==""){alert("PLEASE WRITE YOUR ADDRESS 1")}
    if(this.state.address2==""){alert("PLEASE WRITE YOUR ADDRESS 2")}
    if(this.state.city==""){alert("PLEASE WRITE YOUR CITY NAME")}
    if(this.state.state==""){alert("PLEASE WRITE YOUR STATE")}
    if(this.state.zip==""){alert("PLEASE WRITE YOUR ZIP CODE")}
    if(this.state.country==""){alert("PLEASE WRITE YOUR COUNTRY NAME")}
    if(this.state.checked==""){alert("PLEASE CHECK ON BOX")}
    if(this.state.firstname=="" || this.state.lastname=="" || this.state.phonenumber==""
  || this.state.email=="" || this.state.address1=="" || this.state.address2=="" || this.state.city==""
|| this.state.state=="" || this.state.zip=="" || this.state.country=="" || this.state.checked==""){alert("YOUR DATA IS NOT SUBMITTED YET")}
    else{
    alert("your data is submitted");
    const { title,firstname,lastname,phonenumber,email,address1,address2,city,state,zip,country,checked}=this.state;
    let data={
      title,
      firstname,
      lastname,
      phonenumber,
      email,
      address1,
      address2,
      city,
      state,
      zip,
      country,
      checked,
    }

    
    this.props.adddatachief(data);
    this
    .props
    .history
    .push(Home);
  }
    
  }
  

  render() {

    console.log("state",this.state)
    return (
      <div >
       <Typography variant="h6" gutterBottom>
        Job for chief engineer
      </Typography>
      <Grid container spacing={24}>
        <Grid item xs={12} sm={6}>
          <TextField
             required={true}
            id="firstName"
            name="firstName"
            label="First name"
            fullWidth
            autoComplete="fname"
            
            onChange={(e)=>this.setState({firstname:e.target.value})}
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <TextField
            required={true}
            id="lastName"
            name="lastName"
            label="Last name"
            fullWidth
            autoComplete="lname"
            onChange={(e)=>this.setState({lastname:e.target.value})}
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <TextField
            required={true}
            id="phone number"
            name="phonenumber"
            label="Phone number"
            fullWidth
            autoComplete="phonenumber"
            type="number"
            
            onChange={(e)=>this.setState({phonenumber:e.target.value})}
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <TextField
          type="email"
             required={true}
            id="email"
            name="email"
            label="Email"
            fullWidth
            autoComplete="email"
            
            
            onChange={(e)=>this.setState({email:e.target.value})}
          />
        </Grid>
        <Grid item xs={12}>
          <TextField
             required={true}
            id="address1"
            name="address1"
            label="Address line 1"
            fullWidth={true}
            autoComplete="billing address-line1"
            onChange={(e)=>this.setState({address1:e.target.value})}
          />
        </Grid>
        <Grid item xs={12}>
          <TextField
           required={true}
            id="address2"
            name="address2"
            label="Address line 2"
            fullWidth
            autoComplete="billing address-line2"
            onChange={(e)=>this.setState({address2:e.target.value})}
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <TextField
             required={true}
            id="city"
            name="city"
            label="City"
            fullWidth
            autoComplete="billing address-level2"
            onChange={(e)=>this.setState({city:e.target.value})}
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <TextField id="state" name="state" 
          onChange={(e)=>this.setState({state:e.target.value})} label="State/Province/Region" fullWidth />
        </Grid>
        <Grid item xs={12} sm={6}>
          <TextField
             required={true}
            id="zip"
            name="zip"
            label="Zip / Postal code"
            fullWidth
            autoComplete="billing postal-code"
            onChange={(e)=>this.setState({zip:e.target.value})}
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <TextField
            required={true}
            id="country"
            name="country"
            label="Country"
            fullWidth
            autoComplete="billing country"
            onChange={(e)=>this.setState({country:e.target.value})}
          />
        </Grid>
        <Grid item xs={12}>
          <FormControlLabel
            control={<Checkbox color="secondary" name="saveAddress" value="yes" />}
            label="check if your all information correct"
            onChange={(e)=>this.setState({checked:e.target.value})}
          />
          <Button onClick={this.submitbtn} className="button" variant="contained" color="primary" >
        SUBMIT
      </Button>
        </Grid>
        
      </Grid>

        
      </div>
    )
  }
}


export default connect(null,{adddatachief})(Chief)





















